.footer {
	padding-top: 128px;
	padding-bottom: 122px;
	position: relative;
	overflow: hidden;

	@include tablet {
		padding-bottom: 0;
		padding-top: 40px;
	}

	&:before {
		content: '';
		background: #2b2a29;
		top: 0;
		right: 30px;
		width: 130vw;
		height: 700px;
		transform: rotate(-7deg) translateY(27%);
		border-radius: 100px;
		position: absolute;
		z-index: -1;

		@include tablet {
			transform: unset;
			right: 0;
			width: 100%;
			height: 80%;
			top: auto;
			bottom: 0;
			border-radius: 0;
		}
	}

	.container {
		border-bottom: 1px solid #fff;
		padding-bottom: 27px;
		display: flex;
		align-items: flex-end;

		@include tablet {
			display: block;
			border-bottom: 0;
		}
	}

	&__left {
		width: 50%;
		max-width: 561px;

		@include tablet {
			width: 100%;
		}
	}

	&__logo {
		@include tablet {
			width: 100%;
			text-align: center;

			img {
				max-width: 100%;
			}
		}
	}

	&__info {
		box-shadow: -4px -4px 15px rgba(0, 0, 0, 0.25);
		border-radius: 50px;
		overflow: hidden;
	}

	&__info-top {
		padding: 35px 39px 24px;
		background-color: #fff;
		display: flex;
		justify-content: space-between;

		@include tablet {
			flex-direction: column;
			padding: 30px 25px;
		}
	}

	&__info-links {
		display: inline-flex;
		flex-direction: column;
		align-items: center;

		@include tablet {
			margin-top: 15px;
		}
	}

	&__info-link {
		display: block;
		text-decoration: none;
		color: inherit;
	}

	&__info-tel {
		font-weight: bold;
		font-size: 20px;
		line-height: 20px;
	}

	&__info-mail {
		font-weight: 300;
		font-size: 16px;
		line-height: 25px;
	}

	&__info-bottom {
		background-color: $yellow;
		padding: 27px 19px;
		text-align: center;
		font-size: 17px;

		b {
			font-weight: bold;
		}
	}

	&__mailing {
		margin-top: 37px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		position: relative;

		@include tablet {
			justify-content: flex-start;
			flex-wrap: wrap;
		}
	}

	&__subscribe-txt {
		font-weight: bold;
		font-size: 20px;
		line-height: 20px;
		max-width: 180px;
		margin-left: 31px;
		color: #fff;
	}

	&__subscribe-field-wrap {
		display: flex;
		align-items: center;
		width: 100%;
	}

	&__subscribe-field {
		background: #ffffff;
		border: 1px solid rgba(34, 34, 34, 0.25);
		box-shadow: 0px 20px 30px rgba(34, 34, 34, 0.07);
		border-radius: 50px;
		font: inherit;
		font-size: 12px;
		line-height: 12px;
		padding: 15px 50px 15px 15px;
		margin-left: 15px;
		flex: 1;

		&::placeholder {
			color: rgba(34, 34, 34, 0.2);
		}

		@include tablet {
			width: 100%;
			margin-left: 0;
			margin-top: 15px;
			flex: 1 100%;
		}
	}

	&__subscribe-btn {
		border-radius: 50%;
		height: 40px;
		width: 40px;
		display: flex;
		align-items: center;
		justify-content: center;
		border: 0;
		background-color: $green;
		position: absolute;
		top: 50%;
		right: 4px;
		transform: translateY(-50%);
		transition: ease-in-out 0.18s;

		&:hover {
			background-color: darken($green, 10%);
		}

		@include tablet {
			top: auto;
			bottom: 4px;
			transform: unset;
		}
	}

	&__subscribe-msg {
		color: #fff;
		padding: 0px 20px;
	}

	&__right {
		flex: 1;
		display: flex;
		justify-content: space-around;
		padding-bottom: 25px;

		@include tablet {
			margin-top: 25px;
		}
	}

	&__subtitle {
		font-weight: bold;
		font-size: 20px;
		line-height: 20px;
		color: #fff;
	}

	&__menu {
		ul {
			margin-top: 22px;
		}

		a {
			text-decoration: none;
			color: #fff;
		}
	}

	&__menu-item {
		font-size: 12px;

		&:before {
			content: url("data:image/svg+xml,%3Csvg width='7' height='7' viewBox='0 0 7 7' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='4.5' cy='4.5' r='2.5' fill='%23FFED00'/%3E%3Ccircle cx='2.5' cy='2.5' r='2.5' fill='white'/%3E%3C/svg%3E%0A");
			margin-right: 5px;
		}

		& + .footer__menu-item {
			margin-top: 24px;
		}
	}

	&__contacts {
		ul {
			margin-top: 22px;
		}
	}

	&__contacts-item {
		display: flex;
		align-items: center;

		& + .footer__contacts-item {
			margin-top: 9px;
		}

		img {
			margin-right: 10px;
		}

		a {
			font-weight: bold;
			font-size: 15px;
			line-height: 20px;
			color: $yellow;
			text-decoration: none;
		}
	}

	&__apps {
		display: inline-flex;
		flex-direction: column;
		align-items: flex-start;
		margin-top: 10px;
		position: relative;
	}

	&__apps-soon {
		font-weight: bold;
		font-size: 20px;
		line-height: 20px;
		background: rgba(255, 237, 0, 0.9);
		border-radius: 10px;
		padding: 15px;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	}

	&__app-icon {
		&:last-of-type {
			margin-top: 5px;
		}
	}
}
